import React from "react";
import { ReactSVG } from "react-svg";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { login } from "../../store/user/user-action";
import ErrorMessage from "../../components/Core-Components/ErrorMessage/ErrorMessage"

const Login = () => {
  const [form, setForm] = useState()
  const [error, setError] = useState({})

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const handleChange = ({ target }) => {
    const { name, value } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const handleSubmit = async (ev) => {
    ev.preventDefault()
    if (!form.username || !form.password) return
    try {
      const res = await login(dispatch, form)
      if (res) navigate('/home')
    } catch (err) {
      const errorText = err.response.data.errorMessage
      setError((prevState) => ({ ...prevState, errorText, isError: true }))
    }
  }

  return (
    <div className="body-wrapper bg-color--gradient space-pt--70 space-pb--120">
      {/* auth page header */}
      <div className="auth-page-header space-mb--30">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h3 className="auth-page-header__title">Welcome Back</h3>
              <p className="auth-page-header__text">Log in for best shopping</p>
            </div>
          </div>
        </div>
      </div>

      {error.isError && <ErrorMessage errorMessage={error.errorText} />}

      {/* auth page body */}
      <div className="auth-page-body">
        <div className="container">
          <div className="row">
            <div className="col-12">
              {/* Auth form */}
              <div className="auth-form">
                <form onSubmit={handleSubmit}>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="username">Username</label>
                    <input
                      type="text"
                      name="username"
                      id="username"
                      placeholder="Enter Username"
                      onChange={handleChange}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="password">Password</label>
                    <input
                      type="password"
                      name="password"
                      id="password"
                      placeholder="Enter Password"
                      onChange={handleChange}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--40">
                    <p className="auth-form__info-text">
                      Don't have an account?{" "}
                      <Link to={process.env.PUBLIC_URL + "/register"}>
                        Sign up Now
                      </Link>
                    </p>
                  </div>
                  <button className="auth-form__button">Login</button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* auth page footer */}
      <div className="auth-page-footer">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <span className="auth-page-separator text-center space-mt--20 space-mb--20">
                - OR -
              </span>
              <div className="auth-page-social-login">
                <button>
                  <ReactSVG
                    src={
                      process.env.PUBLIC_URL + "/assets/img/icons/facebook.svg"
                    }
                  />
                  Sign In with Facebook
                </button>
                <button>
                  <ReactSVG
                    src={
                      process.env.PUBLIC_URL + "/assets/img/icons/google.svg"
                    }
                  />
                  Sign In with Google
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
