import { BiErrorCircle } from "react-icons/bi";

const ErrorMessage = ({ errorMessage }) => {

    return (
        <div className="container">
            <div className="space-pt--10 space-pb--10" style={{ background: '#FF8A8A' }}>
                <h5 className="d-flex justify-content-center align-items-center text-center gap-2">
                    <BiErrorCircle />
                    {errorMessage}
                </h5>
            </div>
        </div>
    )
}

export default ErrorMessage;